.loginScreen{
    position: relative;
    height: 100%;
    background: url("https://assets.nflxext.com/ffe/siteui/vlv3/530fc327-2ddb-4038-a3f0-2da2d9ccede1/9c547c8a-e707-4bdb-bdbc-843f134dd2a6/IN-en-20230619-popsignuptwoweeks-perspective_alpha_website_large.jpg")center no-repeat;
    background-size: cover;

}

.loginScreen__gradient{
    width: 100%;
    z-index: 1;
    height: 100vh;
    background: rgba(0,0,0,0.4);
    background-image: linear-gradient(
        
        rgba(0,0,0,0.8)0,
        rgba(0,0,0,0)60%,
        rgba(0,0,0,0.8)100%
    );
}

.loginScreen__body >h2{
    font-size: 1.3rem;
    font-weight:400;
    margin-bottom: 30px;
    /* padding: 20px; */
    
}
.loginScreen__body >h1{
    font-size:3.125rem;
   margin-bottom: 20px;
    
}

.loginScreen__body >h3{
    font-size: 1.3rem;
    font-weight:400;
   
    
}

.loginScreen__body{
    position: absolute;
    top: 30%;
    margin-left:auto;
    margin-right:auto;
    text-align: center;
    z-index: 1;
    color: #fff;
    padding: 20px;
    left: 0;
    right: 0;
}


.loginScreen__button{
    position: fixed;
    right: 20px;
    top:20px;
    padding: 10px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    cursor: pointer;
    font-weight:500;
    border: none;

}

.loginScreen__logo {
    position:fixed;
    left: 0;
    width: 150px;
    object-fit: contain;
    padding-left: 20px;
    margin-top: 20px;
}

.loginScreen__input > form >input {
    padding: 10px;
    outline-width: 0;
    height: 30px;
    width: 30%;
    border: none;
    max-width: 600px;
}

.loginScreen__getStarted{
    padding: 16px 20px;
    font-size: 1rem;
    color:#fff;
    cursor: pointer;
    background-color: red;
    border: none;
    font-weight: 500;

}
.loginScreen__input{
    margin: 20px;
}